<template>
      <div>
          <div class="selectWrapper">
            <select class="user-select" @change="onChange($event)">
                    <option v-for="usuarios in this.nomesId" v-bind:key="usuarios.id" :value="usuarios.id"> User {{usuarios.nome}}</option>
                </select>
          </div>
          <div>
            <div class="row">
                <div>
                    <h2>Name</h2>
                    <div class="showUser">
                        <p>{{this.names[this.usuario]}}</p>
                    </div>
                </div>
                <div>
                    <h2>Username</h2>
                    <div class="showUser">
                        <p>{{this.usernames[this.usuario]}}</p>
                    </div>
                </div>
                <div>
                    <h2>E-mail</h2>
                    <div class="showUser">
                        <p>{{this.emails[this.usuario]}}</p>
                    </div>
                </div>
            </div>
            <div class="row">
                <div>
                    <h2>Phone</h2>
                    <div class="showUser">
                        <p>{{this.phones[this.usuario]}}</p>
                    </div>
                </div>
                <div>
                    <h2>Street</h2>
                    <div class="showUser">
                        <p>{{this.streets[this.usuario]}}</p>
                    </div>
                </div>
                <div>
                    <h2>City</h2>
                    <div class="showUser">
                        <p>{{this.cities[this.usuario]}}</p>
                    </div>
                </div>
            </div>
          </div>
      </div>
</template>
<script>
export default {
  name: 'UserVue',
  props: {
    msg: String
  },
  data(){
    return {
      users: [],
      lista: [],
      names: [],
      nomesId: [],
      usernames: [],
      emails: [],
      phones: [],
      streets: [],
      cities: [],
      userSelect: 'selectUser',
      usuario: 0
    }
  },
  mounted(){
    this.pegaUsuario();


  },
  methods: {
    pegaUsuario(){
      fetch('https://jsonplaceholder.typicode.com/users')
      .then((response) => response.json())
      .then((json) => {
        this.users = json;
        let i;
        for(i = 0; i < 10; i++){
            this.names.push(this.users[i].name)
            this.usernames.push(this.users[i].username)
            this.emails.push(this.users[i].email)
            this.phones.push(this.users[i].phone)
            this.streets.push(this.users[i].address.street)
            this.cities.push(this.users[i].address.city)
            let filtered = this.users.filter(function(el){
            return el.userId === i;

        })
        this.lista.push(filtered)
        filtered = []
        }
        this.nomesId = this.names.map((str, index)=> ({nome: str, id: index +1}))
        console.log(this.nomesId)
        console.log(this.names)

        
      })

    },
    onChange(event){
      this.usuario = event.target.value -1;
    }
  }
}
</script>
<style>
*{
  margin: 0px;
  padding: 0px;
  font: 2em;
  font-family: Verdana, Geneva, Tahoma, sans-serif
}
h2{
  color: rgb(2, 69, 131);
  margin-bottom: 15px;
}
.row{
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.showUser{
    background-color: lightgray;
    border-radius: 10px;
    box-shadow : 0px 4px 4px rgba(0, 0, 0, 0.25);
    width: 250px;
    padding: 10px;
    font-weight: bold;
    margin-bottom: 60px;
}

</style>
